.film-list {
    margin-top: 60px;
    padding-bottom: 60px;
    display: flex;
    flex-wrap: wrap;
    @media screen and (min-width: 320px) {
        margin-right: calc(-1 * 20px);
        margin-bottom: calc(-1 * 20px);
    }
    @media screen and (min-width: 768px) {
        margin-right: calc(-1 * 32px);
        margin-bottom: calc(-1 * 32px);
    }
    @media screen and (min-width: 1024px) {
        margin-right: calc(-1 * 16px);
        margin-bottom: calc(-1 * 32px);
    }
}

.film-list__item {
    @media screen and (min-width: 320px) {
        flex-basis: calc(100% / 1 - 20px);
        margin-bottom: 20px;
        margin-right: 20px;
        max-width: 280px;
    }
    
    @media screen and (min-width: 768px) {
        flex-basis: calc(100% / 2 - 32px);
        margin-bottom: 32px;
        margin-right: 32px;
        max-width: 336px;
    }

    @media screen and (min-width: 1024px) {
        flex-basis: calc(100% / 3 - 16px);
        margin-bottom: 32px;
        margin-right: 16px;
        max-width: 309px;
    }

    &:hover {
        box-shadow: rgba(255, 107, 8) 0px 2px 10px 0px;
        border-radius: 5px;
        transition-duration: 250ms;
        cursor: pointer;

        .film-list__description {
            padding-left: 2px;
        }
    }
}

.film-list__img {
    width: 100%;
    height: 440px;
    border-radius: 5px;
}

.film-list__title {
    margin: 8px 0;
    color: $clr-main;
    font-size: inherit;
    font-weight: 500;
    text-transform: uppercase;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}

.film-list__genres {
    display: inline-block;
    color: $clr-accent;
    font-weight: 500;

    > span {
        margin: 0 3px;
    }
}

.film-list__rating {
    display: inline-block;
    margin-left: 5px;
    width: 36px;
    height: 16px;
    background-color: $clr-accent-btn;
    border-radius: 5px;
}

.error {
    padding: 200px 60px;
    font-size: 24px;
    font-weight: 700;
    text-align: center;
    color: $clr-modal-param;
    font-style: italic;
}

.gallery__library--watched, .gallery__library--queue, .gallery__home--error {
    display: none;
}

// Scroll Up

.fa-arrow-circle-up {
    position: relative;
    background-color: transparent;
    border: transparent;
    cursor: pointer;
    display: none;
}

.scroll-up {
    position: fixed;

    @media screen and (min-width: 320px) {
        top: 85%;
        left: 80%;
    }
    @media screen and (min-width: 768px) {
        top: 85%;
        left: 90%;
    }
    @media screen and (min-width: 1024px) {
        top: 85%;
        left: 95%;
    }
}

